:root{
    --backcolor-main: white;
    --textcolor-day: black;
    --textcolor-night: white;
    --textcolor-second: #495057;
    --textcolor-hover: #5a189a;
}


*{
    scroll-behavior: smooth;
    font-family: monospace;
}
header{
    width: 100vw;
    height: 100px;
    display: flex;
    justify-content: space-around;
    padding: 10px;
    background-color: var(--backcolor-main);
   
}
main{
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 1;
   
    flex-direction: column;

}
.logo{
    width: 10%;
    height: 90%;
 
    background-image: url("../asserts/logo.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: 100% auto;
    
    cursor: pointer;
}

.menu{
    display: flex;
    padding: 3px 6px;
    text-decoration: none;
    height: 90%;
    align-items: center;
    
}
.menu li{
    margin: 0 20px;
    list-style: none;
    color: var(--textcolor-day);
    font-weight: 600;
    font-size: 18px;
    cursor: pointer;
}

.menu li:hover{
    color: #5a189a;
}

.text_main{
    width: 90%;
    display: flex;
    flex-direction: column;
    height:auto;
    text-align: center;
    justify-content: center;
    align-items: center;
    margin: 10px 0;

    
}
.maquina-escrever{

}
.maquina-escrever::after{
    content: "";
    border-right: 4px solid black;
    margin-left: 2px;
    animation: barra .8s infinite ;
}
@keyframes barra {
    50%{
        border-color: transparent;
    }
    
}
.text_main h4{
    width: 70%;
    margin-top: 0;
    color: var(--textcolor-second);
}

.text_main h4:hover{
    color: var(--textcolor-day);

}
.freatures{
    width: 100%;
    padding: 10px;
    height: max-content;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.cards{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 30px;
}
.card_body{
    width: 250px;
    height: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    box-shadow: -3px 20px 11px 0px rgba(0,0,0,0.1);
    justify-content: center;
    cursor: pointer;
    transition: all .8s;
}
.card_body img{
    width: 40%;
    height: auto;
    
}
.card_body:hover{
    scale: 1.1;

}
.section{
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
}
.card_coding{
    width: 35%;
    height: max-content;
    padding: 14px;
    background-color: #131b2e;
    border-radius: 15px;

}
.button_fake{
    width: 5px;
    height: 5px;
    background-color: #ffea00;
    border-radius: 100%;
    padding: 5px;
   
    margin-left: 8px;
}
